var Main_index = 0;
var Main_Title=[];//标题数组
var Main_Content=[];//正文数组

export default {
	data() {
		return {
			Main_index:'',
			Main_Title:'',
			Main_Content:'',
			MainPost_list:[
				{Main_index: 1 , Main_Title : '主板块', Main_Content:'这是主板块'}
			]
				 
		}
	},
	
	methods: {
		det(e){
			uni.navigateTo({
				url:'../det/det'
			})
		},
		Publish(){
			uni.navigateTo({
				url:"../Publish_Editor/Publish_Editor.1"
			})
		}
	},
	onLoad() {
		Main_Title=[];//标题数组
		Main_Content=[];//正文数组
		var title;
		var content;
		uni.getStorage({
			key:"PM_ID",
			success: (res) => {
				for (var i = 1;i <= Main_index ;i++) {
					uni.getStorage({
						key:i + "PM_Title",
						success: (res) => {
							title = res.data;
							Main_Title.push(res.data);
							console.log(Main_Title);
							}
						})
					uni.getStorage({
						key:i + "PM_Content",
						success: (res1) => {
							content = res1.data;
							Main_Content.push(res1.data);
							console.log(Main_Content)
						}
					})
					var Post = {Main_index: i, Main_Title: title , Main_Content: content }
					this.MainPost_list.push(Post)
				}
			}
		})
	}
	
}

